    export default {
        data() {
            return {
                br:'\n',
                imageList: [],
				imgId:'',
				strImgID:'',
				imgArr:[],
				count:0,
                sendData: {
					uid:0,
                    content:'',
					imgIds:''
                },
				
            }
        },
        onLoad() {
            
        },
		onShow() {
			console.log(this.strImgID);
		},
		
        methods: {
            close(e){
                this.imageList.splice(e,1);
            },

            chooseImg() { //选择图片
                uni.chooseImage({
                    sourceType: ["camera", "album"],
                    
                    count: 9 - this.imageList.length,
                    success: (res) => {
                        this.imageList = this.imageList.concat(res.tempFilePaths);
						console.log(this.imageList);
						console.log(res);
                    }
                })
            },

            previewImage() { //预览图片
                uni.previewImage({
                    urls: this.imageList
                });
            },
			
			startUpload(index){
				let userinfo = this.getUserInfo();
				this.sendData.uid = userinfo.id;
				
				uni.uploadFile({
				    url: 'http://localhost:8083/upload/image/return-id', //上传地址
				    filePath: this.imageList[index],
				    name: 'file',
					formData:{
						uid:this.sendData.uid
					},
				    success:res => {
						this.count++;
				    	console.log("上传成功的回调函数");
						console.log(res);
						this.imgArr.push(res.data);
						this.imgId = this.imgId + res.data + ";";
						this.startUpload(this.count);
						
				    }
				});
	
			},
			
            sendRequest() { //发表添加作品请求
			
				console.log(this.sendData);
				uni.request({
					url: this.createApiUrl('works/add'),
					method: 'POST',
					header: {
					        'content-type': 'application/x-www-form-urlencoded'  
					},
					data: this.sendData,
					success: res => {
						console.log(res);
						res = res.data;
						uni.hideLoading();
						if(res.code == 200){
							uni.showToast({
								title:'发表成功',
								duration:1000,
								success:function(){
									uni.switchTab({
										url:'../tabbar/tabbar-1/tabbar-1',
									})
								}
							});
							
						}else{
							uni.showToast({
								title:'发表失败，请稍候再试',
							})
						}
					},
					fail: () => {},
					complete: () => {}
				});
				
				

            },
        
			publishWorks:function(){	
				uni.showLoading({
					title:'发送请求中...',
				})
				this.startUpload(this.count);
				let that = this;
				
				
				setTimeout(function(){
					that.imgId = that.imgArr.join(";");
					console.log(that.imgId);
					that.sendData.imgIds = that.imgId;
					console.log(that.sendData);
					that.sendRequest();
				},10000);
				
				
			}
		
		}
    }
